 *{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Open sans", Helvetica, Arial;
}
:root {
    --theme-blue: #00AEEF;
}
p{
    font-size: 16px;
    font-family: 'Raleway', sans-serif;
    text-align: justify;

}
a {
    text-decoration: none;
}

.main-container{
  min-height: 90vh;
  display: flex;
  flex-direction: column;
}




/* Banner */
#head {
	height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	background: rgba(0, 0, 0, 0.6) url(./assets/images/background.jpeg);
	background-size: cover;
	background-attachment: fixed;
	background-position: center;
	background-blend-mode: overlay;
	color: white;
    /* margin-top: 100px; */
  }

.banner-content{
    align-items: center;
    text-align: center;
	justify-content: center;
  margin: 20px;

}

.banner-content h1{
    color: var(--theme-blue);
	font-size: 74px;
	text-shadow: 1px 1px 1px rgba(0,0,0,0.1);
	font-weight: 900;
	text-align: center;
    font-family: "Open sans", Helvetica, Arial;
}

.banner-content h2{
	color: #fff;
	font-size: 44px;
	text-shadow: 1px 1px 1px rgba(0,0,0,0.1);
	font-weight: 500;
	text-align: center;
    font-family: "Open sans", Helvetica, Arial;
    
}

.banner-content h3{
    color: #fff;
    font-size: 20px;
    text-shadow: 1px 1px 1px rgba(0,0,0,0.1);
    text-align: center;
    font-family: "Open sans", Helvetica, Arial;
    font-weight: 400;
}
.Capi-letter{
    color: var(--theme-blue);
}

.section-heading h2{
    font-size: 36px;
    margin-top: 20px;
	font-weight: 800;
    color: var(--theme-blue);
    border-bottom: 1px solid rgb(201, 200, 200);
    margin-bottom: 20px;

}

.section{
    margin-bottom: 50px;
}



.reach-card-container{
    /* margin: 20px; */
    width: 100%;
    display: flex;
    text-align: left;
    position: relative;

    
}
.reach-card{
    padding: 10px;
    width: 25%;
    border: 1px solid rgb(205, 205, 205);
    text-align: center;
    margin-right: 20px;
    margin-bottom: 20px;
    /* box-shadow: 0rem 0.7rem 2rem rgba(0, 0, 0, 0.5); */

    
}

.reach-card img{
    width: 100%;
    padding: 0.1rem;

}

.reach-card a{
    color: black;
}
.reach-card p{
    text-align: center;
    /* font-size: 20px; */
}
.reach-card a:hover{
    color: var(--theme-blue);
    text-decoration: none;
}





/* Slideshow */

.image-slideshow{
    align-items: center;
}
#contain-image {
  /* position: relative; */
  width: 100%;
}
#contain-image img{
    width: 60%;
    position: relative;
    box-shadow: 0rem 0.7rem 2rem rgba(0, 0, 0, 0.5);
    overflow: hidden;
    text-align: center;
    justify-content: center;
    margin: auto;
  }





.logo-video{
    /* box-shadow: 0rem 0.7rem 2rem rgba(0, 0, 0, 0.5); */
    border: #000 0.1px solid;

}



/* Events */
.thumbnail{
    width: 100%;
    border: 1px solid rgb(193, 192, 192);
    margin-bottom: 20px;
    background-color: rgb(255, 255, 255);
    filter: drop-shadow(5px 5px 5px #222);
    min-height: 550px;

}
.thumbnail img{
    width: 100%;

}
.event-caption{
    padding: 15px;
}

.event-caption h4{
    font-size: 18px;
    font-family: 'Raleway', sans-serif;
    margin-bottom: 10px;
    text-align: center;
}
.event-caption h4:hover{
    color: var(--theme-blue);
}

.event-caption p{
    font-size: 14px;
    font-family: 'Raleway', sans-serif;
}














/* Our Team */
.testinomial{
	font-size: 18px;
    font-family: 'Raleway', sans-serif;
	font-style: italic;
	margin: 30px 0 30px;
    margin-top: 80px;
    padding: 10px;
    text-align: justify;
	border-left: 4px solid rgba(0, 0, 0, 0.13);


}

.image-caption{
	padding: 10px;
}

.container{
    margin-bottom: 10px;
}
.image-caption1{
	font-size: 18px;
	text-align: center;

}
.image-caption2{
	font-size: 14px;
	text-align: center;

}
.udit-card{
	border-radius: 10px;
	width: 100%;
	border-radius: 0;
	box-shadow: 5px 5px 15px #c8c8c8;
	transition: all 0.3s ease-in;
	-webkit-transition: all 0.3s ease-in;
	-moz-transition: all 0.3s ease-in;
    text-align: center;

}
.udit-image{
    width: 100%;
}
.udit-card:hover{
	background:var(--theme-blue);
	color: #fff;
	border-radius: 5px;
	border: none;
	box-shadow: 5px 5px 10px #9e9e9e;
}

.card-caption{
	padding: 5px;
}
.card-body{
    padding: 0;
}
.card-body img{
    width: 100%;
}
.card-caption h3{
	font-size: 16px;
	color: #555;
	text-align: center;
}

.card-caption h5 {
	font-size: 14px;
	color: #333;
	margin-bottom: 10px;
	text-align: center;
}

.team-section{
	margin-bottom: 20px;
}

.team{
	margin-top: 40px;
}
.card {
	border-radius: 0;
	box-shadow: 5px 5px 15px #c8c8c8;
	transition: all 0.3s ease-in;
	-webkit-transition: all 0.3s ease-in;
	-moz-transition: all 0.3s ease-in;
    margin-bottom: 20px;
}

.card:hover {
	background:var(--theme-blue);
	color: #fff;
	border-radius: 5px;
	border: none;
	box-shadow: 5px 5px 10px #9e9e9e;
}

.card:hover h3,
.card:hover i {
	color: #fff;
}





/* Contact form */
.contact-form{
    width: 100%;
}

.form-control{
    width: 100%;
    padding: 10px;
    font-family: 'Roboto', sans-serif;
    font-size: 14px;


}
.form-item{
    padding: 10px;
}
.submit-btn{
    padding: 10px;
    font-size: 14px;
    background-color: var(--theme-blue);
    color: #fff;
    border: none;
}

.submit-btn:hover{
    background-color: rgb(15, 98, 182);
    cursor: pointer;
    color: #fff;
}
.contact-section{
    margin-bottom: 100px;
}
.success-message{
  color: red;
}

.social-buttons{
    padding: 5rem;
}

.social-buttons-icon{
    font-size: 30px;
    padding: 30px 20px;
    outline: none;
    text-decoration: none;
    text-align: center;
    align-items: center;
    justify-content: center;

}

.social-buttons-icon i {
    font-size: 36px;
    color: var(--theme-blue);
}

.social-buttons-icon i:hover{
    color: #015db4;
}








/* Footer */
#footer{
	align-items: center;
	justify-content: center;
	flex-direction: column;
	background: rgba(0, 0, 0, 0.6) url(./assets/images/background.jpeg);
	background-size: cover;
	background-attachment: fixed;
	background-position: center;
	background-blend-mode: overlay;
	color: white;
    text-align: center;
    margin-top: auto;
    
	
}

.footer-nav{
	color: white;
	font-size: 16px;
    padding: 15px;

}
.footer-nav a{
	color: white;
	padding: 0px 5px;
	font-size: 16px;
	text-decoration: none;

}
.footer-nav a:hover{
	color: var(--theme-blue);
}





.emptyList-wrap {
    display: flex;
    justify-content: center;
  }
  .emptyList-wrap img {
    max-width: 250px;
    width: 100%;

  }






.blog-nav{
    text-align: center;
    margin-bottom: 20px;

}

.blog-container {
    overflow: hidden;
    text-align: center;
    align-items: center;
  }
  .filterBtnContainer{
    text-align: center;
  }
  
  .filterDiv {
    float: left;
    background-color: #e9e9e9;
    color: #ffffff;
    /* border: #222 1px solid; */
    width: 250px;
    text-align: center;
    margin: 20px;
    /* display: none;  */
    /* Hidden by default */
  }
  
  /* The "show" class is added to the filtered elements */
  .show-blog {
    display: block;
  }
  
  /* Style the buttons */
  .blog-btn {
    border: none;
    outline: none;
    padding: 12px 16px;
    background-color: #f1f1f1;
    cursor: pointer;
    font-size: 14px;
  }
  
  /* Add a light grey background on mouse-over */
  .blog-btn:hover {
    background-color: #ddd;
  }
  
  /* Add a dark background to the active button */
  .blog-btn.active {
    background-color: var(--theme-blue);
    color: white;
  }
.blog-post{
    background-color: white;
    filter: drop-shadow(5px 5px 5px #222);

}

  .blog-post img{
    width: 100%;
  }
  .blog-section-heading h2{
    font-size: 36px;
    margin-top: 20px;
	font-weight: 800;
	color:var(--theme-blue);
    border-bottom: 1px solid rgb(201, 200, 200);
    margin-bottom: 20px;
}
  .blog-img-caption{
    font-size: 18px;
    padding: 10px;
    font-family: 'Raleway', sans-serif;
    color: black;
  }
  .blog-img-caption:hover{
    color: var(--theme-blue);
    text-decoration: none;
  }


  .blog-side-image{
    width: 100%;
	border: #000 1px solid;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	text-align: center;
	padding: 5px;
    margin-bottom: 20px;
    
}
.blog-side-image img{
    width: 100%;
}
.blog-image-caption{
	font-size: 18px;
	font-style: italic;
	text-align: center;
	background-color: rgb(211, 211, 211);
	padding: 5px 10px;
}
.blog-item p {
    font-size: 18px;
    font-family: 'Montserrat', sans-serif;


}

blockquote {
    font-size: 18px;
	font-style: italic;
    font-family: 'Montserrat', sans-serif;
	color: rgb(0, 0, 0);
	margin: 30px 0 30px;
    padding: 10px;
    text-align: justify;
	border-left: 4px solid rgba(0, 0, 0, 0.13);
}










.blog-wrap {
    max-width: 700px;
    margin: 0 auto;
  }
  
  .blog-goBack {
    text-decoration: none;
    font-size: 0.8rem;
    color: #a9a9a9;
    font-weight: 500;
    margin: 1rem 0;
    display: block;
  }
  .blog-goBack:hover {
    text-decoration: none;
    color: #000000;
  }

  

  .blogList-wrap {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 3rem;
  }
  
  @media (max-width: 768px) {
    .blogList-wrap {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  
  @media (max-width: 600px) {
    .blogList-wrap {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  
  
  
  .blogItem-wrap {
    display: flex;
    flex-direction: column;
  }
  .blogItem-cover {
    width: 100%;
    border: 1px solid black;
    object-fit: cover;
    border-radius: 10px;
    margin-bottom: 0.5rem;
  }
  .blogItem-wrap h3 {
    margin: 0.5rem 0 1rem 0;
    flex: 1;
  }
  

  
  .blogItem-desc::before {
    position: absolute;
    content: '...';
    bottom: 0;
    right: 0;
  }
  

  
  .blogItem-link {
    text-decoration: none;
    color: inherit;
  }
  .blogItem-link:hover{
    text-decoration: none;
    color: var(--theme-blue);
  }
  
  .blogItem-author {
    display: flex;
    align-items: center;
  }
  
  .blogItem-author img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 0.5rem;
  }
  
  .blogItem-author p {
    font-size: 0.6rem;
    color: #a9a9a9;
    font-weight: 600;
  }
  
  
  .emptyList-wrap {
    display: flex;
    justify-content: center;
  }
  .emptyList-wrap img {
    max-width: 250px;
    width: 100%;
  }
  



/* Image Slideshow */

.image-slideshow{
  /* height: 100vh; */
  width: 100%;
  box-sizing: border-box;
  text-align: center;
  display: flex;
}
img {
  vertical-align: middle;
  width: 90%;

}

.prev{
  cursor: pointer;
  width: auto;
  padding: 10px;
  font-weight: bold;
  font-size: 30px;
  user-select: none;
  -webkit-user-select: none;
  border: none;
}
.next {
  cursor: pointer;
  width: auto;
  padding: 10px;
  font-weight: bold;
  font-size: 30px;
  user-select: none;
  -webkit-user-select: none;
  border: none;
}
.prev:hover,
.next:hover {
  color: var(--theme-blue)
}













