
@media (min-width: 1600px){
    .banner-content h1{
        font-size: 94px;
    }
    
    .banner-content h2{
        font-size: 54;
        
    }

    .footer-nav a{
        font-size: 20px;
    
    }
}

@media (max-width: 891px){
    .menu-items li{
        font-size: 16px;
    }
    .logo-image{
        width: 100%;
        height: 32px;
        align-items: center;
        display: flex;
    }
    .about-content p{
        font-size: 16px;
    
    }
    .blog-btn {
        font-size: 12px;
        margin: 2px;
      }
      .blog-item p {
        font-size: 18px;
    
    }
    
    blockquote {
        font-size: 18px;
    }
    .card-caption h3{
        font-size: 14px;
    }
    
    .card-caption h5 {
        font-size: 12px;
    }
    .filterDiv {
        width: 40%;
        margin: 5px 5%;

      }

    
}






@media (max-width: 767px){
    .container{
        width: 95%;
        margin: auto;
    }
    
    .udit-card{
        margin-bottom: 20px;

    
    }
    .image-caption1{
        font-size: 14px;
    
    }
    .image-caption2{
        font-size: 12px;
    
    }
    .testinomial{
        font-size: 18px;
        margin-top: 20px;
    }
    #team .card {
        margin-bottom: 20px;
    }
    .card-caption h3{
        font-size: 14px;
    }
    
    .card-caption h5 {
        font-size: 12px;
    }
    .blog-btn {
        font-size: 10px;
        margin: 2px;
      }
      .blog-item p {
        font-size: 16px;
    
    }
    #contain-image img{
        width: 80%;
      }
      .about-content p{
        margin-bottom: 20px;
    
    }
    
    blockquote {
        font-size: 16px;
    }



.reach-card-container{
    margin: 20px;
    width: 100%;
    display: flex;
    text-align: center;
    position: relative;
    justify-content: center;
    flex-direction: column;
    
}
.reach-card{
    padding: 10px;
    width: 80%;

}

.reach-card p{
    text-align: center;
    color: var(--red);
    font-size: 14px;
}
.prev{
    display: none;
  }
  .next {
    display: none;
  }
  img {
    vertical-align: middle;
    width: 100%;
  
  }
  



}








@media (max-width: 500px){
    
    .banner-content h1{
        font-size: 54px;
    }
    
    .banner-content h2{
        font-size: 36px;
        margin-bottom: 20px;
        
    }
    
    .banner-content h3{
        font-size: 20px;
    }
    .about-content p{
        font-size: 14px;
    
    }
    .footer-nav a{
        font-size: 12px;
    
    }


      .filterDiv {
        width: 90%;
        margin: 5px 10px;

      }
      .blog-img-caption{
        font-size: 12px;
        padding: 5px;
      }
      .thumbnail{
        min-height: 450px;
    
    }
      .event-caption{
        padding: 10px;
    }
    
    .event-caption h4{
        font-size: 16px;
        margin-bottom: 5px;
    }
    
    .event-caption p{
        font-size: 12px;
    }
    .thumbnail{
        margin-bottom: 5px;
    }
    .blog-btn {
        font-size: 8px;
        margin: 2px;
        padding: 5px;
      }
      .blog-item p {
        font-size: 14px;
    
    }
    
    blockquote {
        font-size: 14px;
    }
    .blog-section-heading h2{
        font-size: 20px;
    }
    .blog-image-caption{
        font-size: 10px;
      }
      .social-buttons-icon i {
        font-size: 30px;
    }
    .testinomial{
        font-size: 16px;
        text-align: left;

    
    }
    .section-heading h2{
        font-size: 26px;
    
    }
}





@media (max-width: 380px){

    .banner-content h2{
        font-size: 30px;
        margin-bottom: 30px;
        
    }
    .social-buttons-icon i {
        font-size: 24px;
    }

    .social-buttons-icon{
        font-size: 20px;
        padding: 10px;
    
    }

    .footer-nav a{
        font-size: 10px;
    
    }
    
}