:root {
    --theme-blue: #00AEEF;
    --theme-light-grey: rgb(224, 224, 224);
}

.navbar {
    background: var(--theme-light-grey);
    width: 100%;
    /* height: 60px; */
    display: flex;
    align-items: center;
    z-index: 100;
    justify-content: space-between;
    top: 0;
    position: sticky;
    box-shadow: 5px 0px 10px 0px rgb(85, 84, 84);

}

.logo-image img {
    width: 100px;
    /* height: 50px; */
    align-items: center;
    margin: auto;

    /* display: flex; */
    margin: 5px;
}



.nav-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    /* height: 60px; */

}

.nav-logo {
    align-items: center;
    margin-left: 20px;
    flex-grow: 1;
    /* order: 1; */

}

.nav-menu {
    display: flex;
    list-style: none;
    text-align: center;
    justify-content: space-between;
    margin: auto;

}

.nav-links {
    color: black;
    text-decoration: none;
    padding: 10px 10px;
    font-weight: 500;
    font-family: "Open sans", Helvetica, Arial;
    font-size: 20px;
    align-items: center;

}

.active-nav {
    color: black;
    text-decoration: none;
    padding: 10px 10px;
    font-weight: 500;
    font-family: "Open sans", Helvetica, Arial;
    font-size: 20px;
    align-items: center;
    background-color: var(--theme-blue);

}

.active-nav:hover {
    color: rgb(255, 255, 255);
    text-decoration: none;
    padding: 10px 10px;
    font-weight: 500;
    font-family: "Open sans", Helvetica, Arial;
    font-size: 20px;
    align-items: center;
    background-color: var(--theme-blue);

}

.nav-links:hover {
    background-color: var(--theme-blue);
    color: rgb(255, 255, 255);
    text-decoration: none;

}


.fa-code {
    margin-left: 1rem;
}

.nav-item {
    /* line-height: 50px; */
    margin-right: 0.5rem;
}


.nav-icon {
    display: none;
}



@media screen and (max-width: 767px) {
    .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        position: absolute;
        top: 65px;
        left: -110%;
        opacity: 1;
        transition: all 0.5s ease;
        box-shadow: 5px 0px 10px 0px #aaa;
        min-height: 400px;
    }

    .nav-menu {
        display: flex;
        /* justify-content:flex-start; */
        justify-content: space-around;
        margin: auto;

    }

    .nav-container {
        height: 45px;

    }

    .nav-menu.active {
        background-color: var(--theme-light-grey);
        left: 0px;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav-item {
        margin-right: 0;
    }

    .nav-links {
        /* padding: 10px; */
        width: 100%;
        display: table;
    }

    .active-nav {
        padding: 10px;
        width: 100%;
        display: table;

    }

    .nav-icon {
        display: block;
        position: absolute;
        top: 10;
        right: 20px;
        font-size: 30px;
        cursor: pointer;
        align-items: center;
    }
}